.quick-hide {
  animation-name: fade-out-in;
  animation-duration: .5s;
  animation-timing-function: ease-in-out;
}

@keyframes fade-out-in {
  0% {
    filter: brightness(0);
  }

  65% {
    filter: brightness(0);
  }

  100% {
    filter: brightness();
  }
}

